p{
    margin: 0;
    padding: 0;
}
h1,h2,h3,h4,h5,h6{
    margin: 0;
    padding: 0; 
}


.visually-hidden {
    position: absolute;
    clip: rect(0 0 0 0);
    width: 1px;
    height: 1px;
    margin: -1px;
  }
.section-contacts{
    margin-left: auto;
    margin-right: auto;
    min-width:320px ;
    outline: green 1px solid;
    padding-left: 50px;
    padding-right: 50px;
    background-color:$second-section-color;
    
}
.section-contacts__item{
    
    background-color: white;
    padding: 43px 20px;
    border-radius: 24px;
    box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
    &:not(:last-child){
        margin-bottom:20px ;
    }
    &:last-child{
        margin-bottom: 54px;
    }
}
.section-contacts__btn{
    background-color: yellow;
    width: 80px;
    height: 16px;
    text-align: center;
    margin-bottom: 25px;
    
}
.section-contacts__subtitle{
    font-family: DM Sans;
    font-weight: 500;
    font-size: 12px;
    line-height: 186%;
    color: black;
    
}
.section-contacts__text{
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: $primary-text-color;
    padding-bottom: 25px;
    border-bottom: 1px solid #E1E1E1;
    margin-bottom: 25px;
}
.section-contact__time-information{
    font-weight: 500;
    font-size: 12px;
    line-height: 186%;
    color: $primary-header-color;
    margin-bottom: 10px;
}
.section-contacts__schedule{
    padding-bottom: 25px;
    border-bottom: 1px solid #E1E1E1;
    margin-bottom: 25px;
}
.section-contacts__contact{
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: $primary-text-color; 
}
.section-contact__btn{
font-style: normal;
font-weight: bold;
font-size: 16px;
line-height: 21px;
width: 161.45px;
height: 44px;
border-radius: 22px;
&.one{
    background: rgba(212, 19, 66, 1);
    color: white;
    margin-bottom: 10px;
}
&.two{
    background:white;
    color: $accent-color;
}
}
.section-contact__btn-list{
    text-align: center;
}